import { API, graphqlOperation } from 'aws-amplify'

export default {
  async getMembers () {
    const listTheGinTimeCards = `query listTheGinTimeCards {
      listTheGinTimeCards {
        items {
          date
          memberId
          place
          arrival
          leave
        }
      }
    }`
    const tasks = await API.graphql(graphqlOperation(listTheGinTimeCards))
    return tasks.data.listTheGinTimeCards.items
  },
  async createTask () {
    const createTask = `mutation createTheGinTimeCard($date: String!, $memberId: String!, $place: String) {
      createTheGinTimeCard(
        input: {
          date: $date,
          memberId: $memberId, 
          place: $place
        }
      ) {
        date
        memberId
        place
      }
    }`
    const taskDetails = {
      date: '2019/03/06',
      memberId: '5034189',
      place: 'tama'
    }
    const newTask = await API.graphql(graphqlOperation(createTask, taskDetails))
    return newTask
  },
  async onCreateMember (callback) {
    const subscriptMember = `subscription onCreateTheGinTimeCard {
        onCreateTheGinTimeCard {
          date
          memberId
          place
      }
    }`
    const subscription = API.graphql(graphqlOperation(subscriptMember))
      .subscribe({
        next: function () {
          callback()
        }
      })
    return subscription
  }
}



