<template>
  <div class="Member">
    <v-container
      fluid
      grid-list-lg
    >
      <v-layout row>
        <v-flex xs2></v-flex>
        <v-flex xs8>
          <v-card class="cards" color="primary" v-for="(member, index) in tamaMembers" v-bind:key="index">
            <v-card-title primary-title>
              <div>
                <div class="headline">{{ member.name }}</div>
                <span>{{member.arrival}}</span><br>
              </div>
            </v-card-title>
          </v-card>
          <v-card class="cards" color="green lighten-3" v-for="(member, index) in ginzaMembers" v-bind:key="index">
            <v-card-title primary-title>
              <div>
                <div class="headline">{{ member.name }}</div>
                <span>{{member.arrival}}</span>
              </div>
            </v-card-title>
          </v-card>
        </v-flex>
        <v-flex xs2></v-flex>
      </v-layout>
    </v-container>

  </div>
</template>

<script>
import service from '../services/services'
import moment from 'moment'
export default {
  name: 'hello',
  data () {
    return {
      msg: 'TheGin App',
      tamaMembers: [],
      ginzaMembers: []
    }
  },
  methods: {
    async create () {
      service.createTask()
    },
    async getMembers () {
      let tmpMembers = await service.getMembers()
      let currentDate = moment().format('YYYY/MM/DD')
      let tamaMembers = []
      let ginzaMembers = []
      for (let i = 0; i < tmpMembers.length; i++) {
        if (tmpMembers[i].leave !== null) {
          console.log('' + tmpMembers[i].leave)
          continue
        }
        if (tmpMembers[i].date === currentDate) {
          tmpMembers[i].memberId = Number(tmpMembers[i].memberId)
          if (tmpMembers[i].memberId === 5028447) {
            tmpMembers[i].name = ''
          } else if (tmpMembers[i].memberId === 5034189) {
            tmpMembers[i].name = ''
          } else if (tmpMembers[i].memberId === 5028904) {
            tmpMembers[i].name = ''
          } else if (tmpMembers[i].memberId === 5029125) {
            tmpMembers[i].name = ''
          } else if (tmpMembers[i].memberId === 5029792) {
            tmpMembers[i].name = ''
          } else if (tmpMembers[i].memberId === 5034134) {
            tmpMembers[i].name = ''
          } else if (tmpMembers[i].memberId === 5029486) {
            tmpMembers[i].name = ''
          } else {
            tmpMembers[i].name = ''
          }
          if (tmpMembers[i].place === 'tama') {
            tamaMembers.push(tmpMembers[i])
          } else if (tmpMembers[i].place === 'ginza') {
            ginzaMembers.push(tmpMembers[i])
          }
        }
      }
      this.tamaMembers = tamaMembers
      this.ginzaMembers = ginzaMembers
    }
  },
  mounted () {
    this.getMembers()
    service.onCreateMember(this.getMembers)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.cards{
  margin: 10px;
  padding: 20px
}

.Member{
  text-align: center
}
</style>



